*,
*::before, 
*::after{
  box-sizing: border-box;
}

body,
h1,
h2,
h3
{
  margin:0; 
}

body{
  font-weight: 400;
  font-size:1.3125rem; 
  line-height: 1.6;
  overflow-x: hidden;
  padding: 0px;
  margin: 0px;
}

/* h1,h2,h3{
  font-weight: 900;
  line-height: 1;
} */


.app-name-section{
  background-color: #FB5B16;
  height: 8vh;
  width: 100vw; 
  color: white;
  padding:10px 0px 10px 0px;
}


/* p{
  width: 100vmin;
} */

h1,h5,h6{
  font-family: 'Roboto', sans-serif;
}

@media screen and (min-width: 771px) {
  .app-name-section {
    font-size: 18px;
  }
}

@media screen and (max-width: 770px) {
  .app-name-section {
    font-size: 13px;
  }

  .left-items{
    float:left;
  }

  .right-items{
    margin-left:10vw;
  }

  .mybtn{
    margin-left:5vw;
    /* float:left; */
  }
  .coding1-image{
    float: center;
    height: auto;
    max-width:500px;
    margin-bottom: 1rem;
    /* position: relative; */
  }

  /* .myrow{
    margin-left:0;
  } */

  .mybtn{
    margin-top: 2vh;
  }
  .image{
    max-width:50vh;
    height: auto;
    margin:0;
  }
  h1,h2,h3,h4,h5,h6{
    font-size:1vw;
  }
}

.image{
    max-width: 100vw;
    height: auto;
    overflow: visible;
}

.coding1-image{
  display:inline;
  float: right;
  /* margin-right:5vw; */
  /* margin-right: 5%; */
}

.text-orange{
  color: #FB5B16;
}


.mybtn {
  font-family: 'Roboto';
  color: #ffffff;
  font-size: 1rem;
  background: #FB5B16;
  padding: 5px 30px 5px 30px;
  text-decoration: none;
  border: none;
  border-radius: 14px;
  margin-left: 20vw;
  margin-top: 5vh;
}
/* p{
  width:80vmin;
} */

.firstpage-text{
  margin-left: 20vw;
  margin-top: 20vw;
}
#header-first-page{
  margin-left: 10vw;
  margin-top: 5vw;
}
#p-first-page{
  margin-left: 10vw;
}
.centered-text{
  text-align: center;
}
.backgroundOfFirstPage{
  background: #EEE7E7;
}
#block-text{
  display: block;
  margin-top: 30vh;
}


.btn-second-page{
  margin-top: 20vh;;
  margin-left: 0;
}
.image-second-page{
  margin-top: 20vh;
  margin-left:5vw;
}

.courses-list{
  padding:40px;
}
.purple-text{
  color: #590BB2;
}

.round-btn {
  border-radius: 50%;
  font-family: Arial;
  color: #ffffff;
  border: none;
  background: #590BB2;
  padding: 15px 20px 15px 20px;
  text-decoration: none;
}

.last-page-text{
  color:#373365;
}
.card-text{
  color: #7E57C2;
}
.centered-image{
  margin-left: 30%;
}

.background-third-page{
  background: #EAE7FB;
}